	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Islamic Animal Slaughter To Be Allowed","date":"15 Jan 2002","slug":"/2002-01-15-islamic-animal-slaughter-to-be-allowed/","tags":"Y2002"},"html":"<p><a href=\"http://www.expatica.com/germany.asp?pad=190,205,&amp;item_id=18466\">DPA</a>: The German constitutional court has ruled that the Muslim practice of <a href=\"http://www.library.cornell.edu/colldev/mideast/halalb.htm\">halal butchering</a> (using a knife without stunning) cannot be prohibited. The practice had been banned for animal protection reasons, although the similar practice of kosher butchering had been expressly allowed.</p>"},"next":{"fields":{"title":"Bombs Along The Highway","date":"15 Jan 2002","slug":"/2002-01-15-bombs-along-the-highway/"}},"previous":{"fields":{"title":"Still Running","date":"15 Jan 2002","slug":"/2002-01-15-still-running/"}}},{"node":{"fields":{"title":"Bombs Along The Highway","date":"15 Jan 2002","slug":"/2002-01-15-bombs-along-the-highway/","tags":"Y2002"},"html":"<p>This morning the radio traffic report announced that a section of the Autobahn 7 south of Hamburg would be closed for 30 minutes. Yet another unexploded bomb from the Second World War had been found and was to be defused. Such announcements occur every month or so. But this time I couldn't help but think that if a rich developed country like Germany was still finding remnants of its last war after over 50 years, how long will it take Angola, Bosnia or Afghanistan? Or Ground Zero in Manhatten?<br />\nI then realized that I would be safe in my office during the closure, and drove on.</p>"},"next":{"fields":{"title":"Summer Vacation Is Now","date":"14 Jan 2002","slug":"/2002-01-14-summer-vacation-is-now/"}},"previous":{"fields":{"title":"Islamic Animal Slaughter To Be Allowed","date":"15 Jan 2002","slug":"/2002-01-15-islamic-animal-slaughter-to-be-allowed/"}}},{"node":{"fields":{"title":"Summer Vacation Is Now","date":"14 Jan 2002","slug":"/2002-01-14-summer-vacation-is-now/","tags":"Y2002"},"html":"<p>Chancellor Schröder responded to the candidancy of Edmund Stoiber by saying 1) Stoiber's proposals cannot be financed, and 2) the campaign doesn't start until after the summer break.<br />\nIf 2) is true, why was it necessary to state 1)?</p>"},"next":{"fields":{"title":"No More Of This","date":"14 Jan 2002","slug":"/2002-01-14-no-more-of-this/"}},"previous":{"fields":{"title":"Bombs Along The Highway","date":"15 Jan 2002","slug":"/2002-01-15-bombs-along-the-highway/"}}}],"pathPrefix":"","first":false,"last":false,"index":946,"pageCount":1101}}

